/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .eltd-onsale,
    .eltd-out-of-stock {
        @include eltdWooCommerceMarksGeneralStyle();
    }
    
    .eltd-onsale {
        @include eltdWooCommerceSaleStyle();
    }
    
    .eltd-out-of-stock {
        @include eltdWooCommerceOutOfStockyStyle();
    }
}

.eltd-woo-single-page {
    
    .eltd-single-product-content {
        
        .eltd-onsale {
            left: 10px;
        }
        
        .eltd-out-of-stock {
            right: 10px;
        }
    }
    
    &.eltd-woo-single-thumb-on-left-side {
        
        .eltd-single-product-content {
            
            .eltd-onsale {
                left: 120px;
            }
            
            .eltd-out-of-stock {
                right: 0;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */