/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltd-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .eltd-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltd-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        &:hover{
            color: $default-heading-color;
        }

        .eltd-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            font-size: 20px;
            line-height: 1em;

            .eltd-cart-number{
                position: absolute;
                top: -4px;
                right: -5px;
                font-family: $default-text-font;
                color: $default-heading-color;
                font-size: 9px;
                width: 15px;
                height: 15px;
                border-radius: 50%;
                background-color: $first-main-color;
                font-weight: 400;
                text-align: center;
                line-height: 15px;
            }

            &:before {
                display: block;
                line-height: inherit;
            }
        }
    }

    &:hover {

        .eltd-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.eltd-dark-header .eltd-page-header > div:not(.eltd-sticky-header):not(.fixed) .eltd-shopping-cart-holder .eltd-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltd-light-header .eltd-page-header > div:not(.eltd-sticky-header):not(.fixed) .eltd-shopping-cart-holder .eltd-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltd-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 352px;
    background-color: #fff;
    border: 1px solid $default-border-color;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include eltdTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 30px;
        padding: 0;

        li {
            @include eltdTableLayout();
            padding: 0 0 20px;
            box-sizing: border-box;

            &.eltd-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .eltd-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 70px;
        vertical-align: top;

        img {
            display: block;
            border: 1px solid #dddddd;
        }
    }

    .eltd-item-info-holder {
        @include eltdTableCellLayout();
        vertical-align: top;
        padding-left: 20px;

        .eltd-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            text-transform: uppercase;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                    margin-top: 3px;
                }
            }
        }

        .eltd-quantity {
            display: block;
            margin: -3px 0 0;
            font-size: 13px;
            font-weight: 300;
            font-style: italic;
            letter-spacing: .08em;
            text-transform: lowercase;
            color: $default-heading-color;
        }

        .amount {
            display: block;
            margin: -3px 0 0;
            color: $default-heading-color;
            font-size: 13px;
            font-weight: 300;
            font-style: italic;
            letter-spacing: .08em;
            text-transform: lowercase;
        }
    }

    .eltd-cart-bottom {
        padding: 0;

        .eltd-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 13px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }

            .eltd-total-amount {
                text-align: right;
            }
        }

        .eltd-btn-holder{
            display: inline-block;
            float: left;
            width: auto;
        }

        .eltd-view-cart,
        .eltd-view-checkout{
            display: inline-block;
            margin: 0 0 12px;
            height: 42px;
            width: 135px;
            line-height: 38px;
            padding: 0 26px;
            font-size: 10px;
            font-weight: 600;
            text-transform: uppercase;
            color: #000;
            background-color: #fff;
            border: 1px solid  #000;
            text-align: center;
            box-sizing: border-box;
            letter-spacing: 0.1em;
            @include eltdTransition(color .2s ease-out, background-color .2s ease-out);
        }

        .eltd-view-cart{
            margin-right: 16px;
        }
    }
}

.eltd-vertical-menu-area {

    .eltd-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */