/* ==========================================================================
   Product Category shortcode styles - begin
   ========================================================================== */

.eltd-category-holder {
    position: relative;

    .eltd-category-image {
        position: relative;

        img {
            display: block;
            @include eltdTransition(all 0.35s ease-in-out);
            @include eltdTransform(translateZ(0));
            backface-visibility: hidden;
        }

        .eltd-category-text-wrapper {
            position: absolute;
            width: 100%;
            text-align: center;
            bottom: 0;

            .eltd-category-title{
                margin: 0;
            }
            .eltd-category-tag{
                margin: 3px 0 28px;
            }
        }
    }

    &.light-skin{
        .eltd-category-text-wrapper{
            .eltd-category-text{
                > *{
                    color: #fff;
                }
            }
        }
    }

}

/* ==========================================================================
   Product Category shortcode styles - end
   ========================================================================== */