/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.eltd-pl-holder {
    @include eltdRelativeHolderLayout();

    .eltd-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;

        .eltd-pli-onsale,
        .eltd-pli-out-of-stock {
            @include eltdWooCommerceMarksGeneralStyle();
        }

        .eltd-pli-onsale{
            @include eltdWooCommerceSaleStyle();
        }

        .eltd-pli-out-of-stock{
            @include eltdWooCommerceOutOfStockyStyle();
        }

        &:hover{
            .eltd-pli-text-inner{
                @include eltdTransform(translateY(0));
            }
        }

	    .eltd-pli-title {
		    margin: 0 0 3px;
	    }
	
	    .eltd-pli-category {
		    margin: 0 0 3px;
            text-transform: lowercase;
	    }

        .eltd-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .eltd-pli-rating-holder {
		    @include eltdRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .eltd-pli-rating {
		    @include eltdWooCommerceRatingsStyle();
	    }

        .eltd-pli-price {
	        @include eltdWooCommercePriceStyle();
            margin: 8px 0 0;
        }
    }

    .eltd-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;
        border: 1px solid $default-border-color;

        .touch & {
            cursor: pointer;
        }

        .eltd-pli-image {
            @include eltdRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }
        }
            
        .eltd-pli-text {
            @include eltdAbsoluteHolderLayout();
            padding: 0;
            box-sizing: border-box;
        }

        .eltd-pli-text-outer {
	        @include eltdTableLayout();
	        text-align: center;
        }

        .eltd-pli-text-inner {
            position: absolute;
            display: block;
            height: auto;
            bottom: 0;
            width: 100%;
            vertical-align: bottom;
            background-color: #fff;
            border-top: 1px solid #ddd;
            @include eltdTransform(translateY(100%));
            @include eltdTransition(all .2s ease-in-out);

            .button,
            .added_to_cart {
                padding: 8px 0 15px;
                font-size: 18px;
                font-family: $default-heading-font;
                font-weight: 300;
                font-style: italic;
                letter-spacing: .08em;
                color: #1c1c1c;
                background-color: transparent;
                position: relative;
                text-transform: lowercase;
                display: inline-block;

                &:hover{
                    background-color: transparent;
                    color: #1c1c1c;
                }

                &:after{
                    content: '';
                    position: absolute;
                    left: 0;
                    bottom: 13px;
                    width: 100%;
                    height: 4px;
                    background-color: #ffd94a;
                }
            }

            .button.added {
                display: none;
            }
        }

	    .eltd-pli-link {
		    @include eltdAbsoluteHolderLayout();
	    }
    }

    .eltd-pli-text-wrapper {
        @include eltdRelativeHolderLayout();
        margin: 17px 0 18px;
        text-align: left;
    }
	
	/*************** Standard Layout - begin ***************/
	
	&.eltd-standard-layout {
		
		.eltd-pli {
			float: left;
		}
		
		&.eltd-large-space {
			margin: 0 0 -50px;
		}
		
		&.eltd-normal-space {
			margin: 0 0 -30px;
		}
		
		&.eltd-small-space {
			margin: 0 0 -20px;
		}
		
		&.eltd-tiny-space {
			margin: 0 0 -10px;
		}
		
		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($standard_list_columns) {
			&.eltd-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.eltd-pli {
					width: $column_width;
					
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
	
	/*************** Standard Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */