/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .eltd-content,
div.woocommerce {

    .eltd-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .eltd-quantity-input,
        .eltd-quantity-minus,
        .eltd-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid #ddd;
            text-align: center;
            box-sizing: border-box;
        }

        .eltd-quantity-input {
            position: relative;
            height: 63px;
            width: 63px;
            padding: 0;
            margin: 0 31px 0 0;
            font-family: $default-heading-font;
            color: #231f20;
            font-weight: 600;
            font-size: 13px;
            font-style: normal;
            line-height: 63px;
            outline: 0;
        }

        .eltd-quantity-minus,
        .eltd-quantity-plus {
            position: absolute;
            right: 0;
            width: 31px;
            height: 32px;
            line-height: 32px;
            border-left: 0;
            cursor: pointer;
            font-size: 15px;
            color: #231f20;
            @include eltdTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .eltd-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .eltd-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .eltd-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        position: relative;
        width: 100%;
        margin: 0 0 14px;
        padding: 12px 20px;
        font-family: inherit;
        font-size: 12px;
        line-height: 18px;
        font-weight: inherit;
        color: $default-text-color;
        background-color: transparent;
        border: 1px solid #ddd;
        outline: 0;
        -webkit-appearance: none;
        box-sizing: border-box;
        font-style: italic;
        text-transform: lowercase;
        letter-spacing: .08em;

        &:focus {
            border-color: $default-heading-color;
        }
    
        @include eltdPlaceholder {
            color: inherit;
        }
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */