/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltd-woo-single-page {
	
	&.eltd-woo-single-thumb-on-left-side {
		
		.eltd-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include eltdRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .eltd-single-product-content {
        @include eltdRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 48%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include eltdRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }

                .eltd-onsale{
                    left: 0;
                }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
                    border: 1px solid #ddd;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .eltd-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 0 0 0 29px;
        float: left;
        box-sizing: border-box;
	
	    .eltd-single-product-title {
		    display: block;
		    margin: 0;
            position: relative;
            top: -8px;
	    }
	
	    .price {
		    @include eltdWooCommercePriceStyle();
            margin: 5px 0 0;
            font-size: 18px;
            letter-spacing: 1px;
	    }
	
	    .woocommerce-product-rating {
		    @include eltdRelativeHolderLayout();
		    line-height: 1;
		    margin: 0 0 3px;
            top: -6px;

            a{
                font-family: $default-heading-font;
                font-weight: 300;
                font-style: italic;
                font-size: 13px;
                letter-spacing: 1px;
            }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include eltdRelativeHolderLayout();
		    margin: 44px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 52px 0 0;
		
		    > span {
			    display: block;
			    margin: 3px 0;
                font-family: $default-heading-font;
                font-size: 18px;
                font-weight: 300;
                font-style: italic;
                text-transform: lowercase;
                color: #393939;

                > span{
                    color: $default-text-color;
                    font-size: 13px;
                    font-style: italic;
                    letter-spacing: 1px;
                }
			
			    a {
				    color: $default-text-color;
                    font-size: 13px;
                    font-style: italic;
                    letter-spacing: 1px;

                    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .eltd-woo-social-share-holder {
		    @include eltdRelativeHolderLayout();
            margin: 42px 0 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
                margin: 3px 0;
                padding-right: 18px;
                font-family: $default-heading-font;
                font-size: 13px;
                font-weight: 300;
                font-style: italic;
                text-transform: lowercase;
                color: #393939;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include eltdRelativeHolderLayout();
		    margin: 42px 0 0;

            button.single_add_to_cart_button{
                float: left;
                font-size: 13px;
                font-family: $default-heading-font;
                font-weight: 600;
                letter-spacing: .05em;
                background-color: transparent;
                border: 1px solid #000;
                color: #000;
                padding: 17px 51px;

                &:hover{
                    background-color: transparent;
                    border: 1px solid #000;
                    color: #000;
                }
            }
	    }

	    .eltd-quantity-buttons {
		    margin: 0 0 0 30px;
	    }
    }
	
	.woocommerce-tabs {
		@include eltdRelativeHolderLayout();
		margin: 30px 0 0;
		
		ul.tabs {
			@include eltdRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 1px solid $default-border-color;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 7px 54px 7px 0;
                    font-size: 16px;
                    font-weight: 700;
                    text-transform: uppercase;
                    font-family: $default-heading-font;
                    color: #000;

                    &:after{
                        content: '';
                        background-color: $first-main-color;
                        height: 4px;
                        width: 50px;
                    }
				}

                &.active,
                &:hover{
                    a{
                        &:after{
                            content: '';
                            background-color: #ffd94a;
                            height: 4px;
                            width: 50px;
                            display: block;
                            position: absolute;
                            bottom: -4px;
                        }
                    }
                }
			}
		}
		
		.entry-content {
			@include eltdRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 33px 0 20px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				width: 1%;
				padding: 0 10px 0 0;
                font-family: $default-heading-font;
                font-size: 18px;
                font-weight: 300;
                font-style: italic;
                text-transform: lowercase;
                color: #393939;
			}
			
			td {
				width: 99%;
                font-family: $default-heading-font;
                font-size: 13px;
                font-weight: 300;
                font-style: italic;
                text-transform: lowercase;
                color: #777;
			}
		}
		
		#reviews {
			@include eltdRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include eltdRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include eltdWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;

		> h2 {
			display: block;
			margin: 36px 0 42px;
			text-align: left;
            font-size: 24px;
            font-weight: 700;
            letter-spacing: .1em;
		}
		
		.product {
			
			.eltd-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */